import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.css']
})
export class BreadcrumbComponent implements OnInit {

  breadcrumbs: Array<Object>;
  title: string = "";
  sub: any = "";
  constructor(private router: Router, private route: ActivatedRoute) {
    this.router.events.subscribe((val) => {
      // see also 
      if (val instanceof NavigationEnd) {
        if (val.url.includes("/activities/new")) {
          this.title = "Qun l hot ng";
          this.sub = "To mi"
        } else if (val.url.includes("/activities/points")) {
          this.title = "im danh";
          this.sub = "im danh"
        }
        else if (val.url.includes("/activities")) {
          this.title = "Qun l hot ng";
          this.sub = "Danh sch"
        }
        else if (val.url.includes("/users/new")) {
          this.title = "To ti khon";
          this.sub = "Ti khon"
        }
        else if (val.url.includes("/users")) {
          this.title = "Qun l ti khon";
          this.sub = "Ti khon"
        } else if (val.url.includes("/organizations")) {
          this.title = "Qun l t chc";
          this.sub = "T chc"
        }
      }
    });
  }

  ngOnInit() {
  }

}



